:root {
  --color-3: #D85416;
  --font-stack-body: "cartograph_cf", "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  --font-stack-heading: "strange_times", Georgia, "Times New Roman", Times, serif;
}

html {
  background: none;
}

html::before, footer::after {
  display: none;
}

body {
  background: url("/assets/images/contour-bg.svg") 20% 25em no-repeat #FDF7F6;
}

main {
  padding: 0;
}
main p {
  padding-left: 2em;
  padding-right: 2em;
}

h1 {
  font-size: 3em;
  width: 60%;
  margin: 0 auto;
}

h1::after {
  content: "";
  display: block;
  position: absolute;
  width: 3.6em;
  height: 2.5em;
  margin-left: calc(50% - 1.8em);
  z-index: -1000;
  background: url("/assets/images/vertigo-spiral.svg") no-repeat;
  background-size: contain;
  bottom: 0.2em;
}

h1 span {
  display: block;
  margin-top: 0;
}

h1 span:first-child {
  transform: rotate(5deg);
  position: relative;
  right: 0.5em;
}

h1 span:last-child {
  transform: rotate(3deg);
  position: relative;
  left: 1em;
}

img {
  margin: 2em auto;
}

#original-poster {
  transform: rotate(-2deg);
  max-width: 10em;
}

main p:first-of-type {
  margin-top: 8em;
}

footer {
  height: 100%;
}

@media (min-width: 600px) {
  footer {
    height: 100%;
    margin-top: 10em;
  }

  footer::after {
    content: "";
    display: block;
    position: absolute;
    bottom: 0;
    right: 0;
    z-index: -1001;
    background: url("/assets/images/contour-bg.svg") no-repeat;
    background-size: cover;
    height: 16em;
    width: 50vw;
  }

  main {
    transform: rotate(-2deg);
    position: relative;
    left: -2em;
  }

  body {
    background-position: 20% 35em;
  }

  #strange-times-quote {
    transform: rotate(2deg);
  }
}
@media (min-width: 1000px) {
  h1 {
    width: 30%;
    position: relative;
    left: 1em;
  }
  h1 span:first-child {
    right: 2em;
  }

  main {
    left: -8em;
  }

  #main-image {
    position: relative;
    left: -5em;
    transform: rotate(1deg);
  }

  .sideimg {
    position: absolute;
    left: 36em;
  }

  #original-poster {
    bottom: 26em;
    left: 38em;
  }
}